*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --main-font: "Open Sans", sans-serif;
  --secondary-font: "Raleway", sans-serif;
  --main-txt-cl: #303030;
  --accent-txt-cl: #ff6c00;
  --light-txt-cl: #fff;
  --dark-txt-cl: #555;
  --grey-cl: #9da4bd;
  --grey-cl-12: rgba(157, 164, 189, .12);
  --grey-cl-40: rgba(157, 164, 189, .4);
  --grey-cl-60: rgba(157, 164, 189, .6);
  --dark-bg-cl: #191c26;
  --dark-bg-cl-40: rgba(25, 28, 38, .4);
  --dark-bg-cl-60: rgba(25, 28, 38, .6);
  --accent-bg-cl: #ff6c00;
  --light-bg-cl: #f7f7f7;
  --border-light-cl: #fff;
  --btn-radius: 25px;
  --anim-fast: 400ms ease-in-out;
  --anim-slow: 800ms ease-in-out;
  --indent: 30px;
  --items: 3;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@keyframes heartBeat {
  0% {
    transform: translate(-50%, -50%);
  }

  20% {
    transform: translate(-50%, -50%)scale(1.15);
  }

  40% {
    transform: translate(-50%, -50%)scale(.9);
  }

  60% {
    transform: translate(-50%, -50%)scale(1.1);
  }

  80% {
    transform: translate(-50%, -50%)scale(1);
  }

  100% {
    transform: translate(-50%, -50%)scale(1);
  }
}

.price-list, .about__list, .nav__list, .mobile-media-list, .mobile-menu-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

html {
  scroll-behavior: smooth;
}

body {
  font-size: 16px;
  line-height: 1.63;
  font-family: var(--main-font);
  color: var(--main-txt-cl);
}

@media screen and (min-width: 1200px) {
