.backdrop {
  width: 100%;
  height: 100%;
  z-index: 1000;
  transition: opacity var(--anim-fast), visibility var(--anim-fast);
  background-color: rgba(0, 0, 0, .3);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop--hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  width: 528px;
  min-height: 580px;
  background-color: #fff;
  border-radius: 10px;
  padding: 40px;
  animation: heartBeat 1s ease-in-out .2s both;
  position: absolute;
  top: 50%;
  left: 50%;
}

.backdrop--hidden .modal {
  animation-play-state: paused;
}

.modal-btn {
  width: 30px;
  height: 30px;
  color: var(--light-txt-cl);
  background-color: var(--accent-bg-cl);
  transition: color var(--anim-fast), background-color var(--anim-fast), box-shadow var(--anim-fast);
  cursor: pointer;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: inline-flex;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(50%, -50%);
}

